export default () => {
  return (
    <div>
      <div className="container">
        <div className="about">
          <span className="title">About</span>
        </div>
        <div className="get-post">Post</div>
        <div className="contact">
          <span className="title">Contact</span>
        </div>
      </div>
      <style jsx>{`
        .container {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          overflow: hidden;
          font-family: 'Lato', Calibri, Arial, sans-serif;
        }
        .about {
          position: absolute;
          width: 50%;
          height: 100%;
          left: 0;
          top: 0;
          background-color: #FA987D;
        }
        .contact {
          position: absolute;
          width: 50%;
          height: 100%;
          top: 0;
          left: 50%;
          background-color: #10A296;
        }
        .title {
          display: block;
          text-align: center;
          line-height: 100%;
        }
        .get-post {
          position: absolute;
          width: 100px;
          height: 100px;
          top: 50%;
          margin-top: -50px;
          left: 50%;
          margin-left: -50px;
          background-color: #f2ede3;
          border-radius: 50%;
          z-index: 10;
          text-align: center;
          line-height: 100px;
          font-weight: bold;
          font-size: 20px;
          color: #333;
          cursor: pointer;
        }
      `}</style>
    </div>
  );
}



